<template>
	<div id="mall-news">
		<public-header title="资讯"></public-header>
		<div class="content">
			<van-swipe :autoplay="3000" indicator-color="#ff2d4b" v-if="0">
				<van-swipe-item>
					<img class="swipe-img" src="https://img.yzcdn.cn/vant/apple-1.jpg" alt="">
				</van-swipe-item>
				<van-swipe-item>
					<img class="swipe-img" src="https://img.yzcdn.cn/vant/apple-2.jpg" alt="">
				</van-swipe-item>
			</van-swipe>
			<van-tabs v-model="filter.items.cateid" @change="onToggleStatus" class="van-hairline--top" sticky :offset-top="46" line-width="22%">
				<van-tab :title="itemCategory.title" v-for="(itemCategory, indexCategory) in newsCategorys" :key="indexCategory" :name="itemCategory.id">
					<van-list v-model="news.loading" :finished="news.finished" @load="onLoad" :offset="100" :immediate-check="true">
						<template v-if="news.data.length > 0">
							<template v-for="(item, index) in news.data">
								<div class="news-item" :key="index" @click="util.jsUrl('/mall/pages/news/detail', {id: item.id})">
									<van-row gutter="10">
										<van-col span="18">
											<div class="news-title">{{item.title}}</div>
											<div class="news-date">{{item.addtime_cn}}</div>
										</van-col>
										<van-col span="6">
											<div class="news-thumb">
												<img :src="item.thumb" alt="">
											</div>
										</van-col>
									</van-row>
								</div>
							</template>
							<load type="loaded" text="我是有底线的" bgcolor="#f5f5f5" v-if="news.finished"></load>
						</template>
						<div class="common-no-con" v-else>
							<img src= "static/img/order_no_con.png" alt="" />
							<p>暂无资讯！</p>
						</div>
					</van-list>
				</van-tab>
			</van-tabs>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import load from '@/components/load'
	export default {
		components: {
			PublicHeader,
			load,
		},
		data() {
			return {
				newsCategorys: {},
				news: {
					page: 1,
					psize: 10,
					finished: false,
					loading: false,
					data:[]
				},
				filter: {
					items: {
						cateid: 0
					}
				},
				showPreLoading: true,
			}
		},

		methods: {
			onToggleStatus(status) {
				this.filter.items.cateid = status;
				this.onLoad(true)
			},

			onLoad(force) {
				if(force) {
					this.news = {
						page: 1,
						psize: 10,
						loading: true,
						finished: false,
						data:[]
					};
				}
				if(this.news.finished) {
					return false;
				}
				this.util.request({
					url: 'wmall/home/news/list',
					data: {
						page: this.news.page,
						psize: this.news.psize,
						cateid: this.filter.items.cateid,
					}
				}).then((res) => {
					let result = res.data.message;
					if(result.errno) {
						this.$toast(result.message);
						return;
					}
					this.hideLoading();
					result = result.message;
					this.newsCategorys = result.categorys;
					this.news.data = [...this.news.data, ...result.news];
					this.news.loading = false;
					if(result.news.length < this.news.psize) {
						this.news.finished = true;
					}
					this.news.page++;
				});
			},

			hideLoading(){
				this.showPreLoading = false;
			},
		},

		created() {
			this.query = this.util.parseQuery(this.$route.query);
			if(this.query.cid) {
				this.filter.items.cateid = this.query.cid;
			}
		},

		mounted() {
			this.onLoad();
		},

	}
</script>

<style>
	#mall-news .van-swipe-item{
		font-size: 0px;
	}
	#mall-news .van-swipe-item .swipe-img{
		width: 100%;
		height: 150px;
	}
	#mall-news .news-item{
		margin: 10px 10px;
		padding: 15px;
		background-color: #fff;
		border-radius: 5px;
		font-size: 14px;
	}
	#mall-news .news-item .news-title{
		width: 100%;
		overflow : hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		font-size: 14px;
		line-height: 1.5;
		font-weight: 500;
	}
	#mall-news .news-item .news-date{
		margin-top: 20px;
		font-size: 12px;
		color: #ccc;
	}
	#mall-news .news-item .news-thumb{
		position: relative;
		width: 100%;
		padding-top: 75%;
		overflow: hidden;
	}
	#mall-news .news-item .news-thumb img{
		position: absolute;
		z-index: 1;
		top: 0;
		bottom: 0;
		width: 100%;
		border-radius: 5px;
	}
	#mall-news .common-no-con{
		position: fixed;
		z-index: 5;
		top: 40%;
		left: 0;
		width: 100%;
		height: 200px;
		text-align: center;
		color: #768491;
		font-size: 16px;
	}
	#mall-news .common-no-con img{
		max-width: 25%;
		vertical-align: middle;
	}
	#mall-news .common-no-con p{
		margin: 20px 0;
	}
	#mall-news .common-no-con .common-no-con-a{
		display: inline-block;
		height: 35px;
		line-height: 35px;
		color: #fff;
		font-size: 14px;
		background: #ff2d4b;
		padding: 0 10px;
		border-radius: 35px;
	}
</style>


